
.navbar {
    overflow: hidden;
    min-height: 60px;
    z-index: 100;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    /* background: var(--navbar-background); */
    /* background: rgba(248, 249, 250, 1); */
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
}

.navbar a {
    color: inherit;
    text-decoration: none;
    display: flex;
}

.nav-item {
    background: transparent;
    box-sizing: border-box;
    color: inherit;
    display: flex;
    max-width: 70%;
}

.nav-item  select {
    /* max-width: 50%; */
    color: inherit;
    background: inherit;
    text-align: center;
    margin: auto;
    padding: 1rem 0.5rem 1rem 0.5rem;
    display: inline-block;
    height: fit-content;
    border-radius: 0.25em;
    font-size: var(--body-text);
}

#book-select {
    margin-right: 0.5rem;
    max-width: 50%;
}

#chapter-select {
    margin-left: 0.5rem;
    padding-right: 0;
    max-width: fit-content;
}

.bookmark-button {
    margin-right: 0rem;
    justify-items: flex-end;
}

.nav-item:first-child {
    margin: 0.5rem 0rem 0.5rem 2rem;
}

.nav-item input {
    max-width: 50%;
    color: #363535;
    background: inherit;
    text-align: center;
    padding: 1rem 0rem 1rem 0rem;
    height: fit-content;
    border-style: solid;
    border-radius: 0.25em;
    font-size: var(--body-text);
}

.nav-item button {
    background: inherit;
    border-style: solid;
    border-radius: 2em;
    padding: 0.1rem 0.5rem 0.1rem 0.5rem;
    height: fit-content;
    margin-left: 0.5rem;
}
